<template>
  <div id="main">
    <div class="header">
      <nav class="navbar navbar-light bg-faded" style="background-color:#3775a8;">
        <div class="container">
          <a v-link="'welcome'">
            <img src="./assets/logo.png" style="margin-top:-5px;" />
          </a>

          <user-bar></user-bar>
        </div>
      </nav>
    </div>
    <div class="container" style="padding-top:50px;">
      <div class="alert alert-success" v-if="canHandle">
        <button type="button" class="close" @click="hideHandleTipModal">
          <span aria-hidden="true">&times;</span>
        </button>
        <strong>一起顺利!</strong> 操作成功.
      </div>
      <div class="row">
        <left-bar></left-bar>
        <div :class="colSm">
          <div :class="rightContent">
            <router-view></router-view>
          </div>
        </div>
      </div>
    </div>
    <footer class="bd-footer text-muted">
      <div class="container">
        Copyright 2013 &copy; - <a href="#">动起来软件公司</a> - <a href="http://www.miibeian.gov.cn/">粤ICP备15005696号-3</a>
      </div>
    </footer>
    <div class="modal-mask" v-if="loading">
      <div class="modal-wrapper">
        <div class="modal-container">
    	    <img src="./assets/oval.svg" width="50" alt="" style="margin-right:10px;">
          <span>请耐心等待......</span>
        </div>
      </div>
    </div>
    <div class="modal-mask" v-show="hasError" transition="modal">
      <div class="modal-wrapper">
        <div class="modal-container">
          <div class="modal-header">
            <span>请注意</span>
            <i class="fa fa-close pull-xs-right" @click="hideAlertModal"></i>
          </div>
          <div class="modal-body">
            {{message}}
          </div>
          <div class="modal-footer">
            <a @click="hideAlertModal" class="weui_btn_dialog primary">确定</a>
          </div>
        </div>
      </div>
    </div>

  </div>
</template>
<script>
  import store from './vuex/store'
  import { signin, showAlert, hideAlert, showHandleTip, hideHandleTip } from './vuex/actions'

  export default {
    store,
    vuex: {
      getters: {
        loading: state => state.loading,
        isLogged: state => state.merchant.isLogged,
        hasError: state => state.errors.hasError,
        message: state => state.errors.message,
        canHandle: state => state.canHandle
      },
      actions: {
        signin,
        showAlert,
        hideAlert,
        showHandleTip,
        hideHandleTip
      }
    },
    methods: {
      hideAlertModal () {
        this.hideAlert()
      },
      hideHandleTipModal () {
        this.hideHandleTip()
      }
    },
    computed: {
      colSm () {
        return this.isLogged ? 'col-sm-9' : 'col-sm-12'
      },
      rightContent () {
        return this.isLogged ? 'card card-block right-content' : ''
      }
    },
    components: {
      'left-bar': require('./components/left-bar.vue'),
      'user-bar': require('./components/user-bar.vue')
    },
    created () {
      if (!this.isLogged) {
        let access_token = localStorage.getItem('jc_merchant_access_token')
        if (access_token !== null) {
          this.$http.post('http://jiancan.me/api/m1/merchants/authenticate.json', { access_token: access_token }).then(function (response) {
            this.signin(response.data)
          }, function (response) {
            this.$route.router.go({name: 'signin'})
          })
        } else {
          this.$route.router.go({name: 'signin'})
        }
      }
    }
  }
</script>



