import * as types from './mutation-types'

export const signin = ({ dispatch }, merchant) => {
  dispatch(types.SIGN_IN, merchant)
  dispatch(types.ROUTE_CHANGED, { path: '/shop', query: {}, params: {} })
}

export const signout = ({ dispatch }) => {
  dispatch(types.SIGN_OUT)
  dispatch(types.ROUTE_CHANGED, { path: '/signin', query: {}, params: {} })
}

export const showAlert = ({ dispatch }, messages) => {
  dispatch(types.SHOW_ALERT, messages)
}

export const hideAlert = ({ dispatch }, messages) => {
  dispatch(types.HIDE_ALERT)
}

export const showLoading = ({ dispatch }) => {
  dispatch(types.SHOW_LOADING)
}

export const hideLoading = ({ dispatch }) => {
  dispatch(types.HIDE_LOADING)
}

export const showHandleTip = ({ dispatch }) => {
  dispatch(types.SHOW_HANDLE_TIP)
}

export const hideHandleTip = ({ dispatch }) => {
  dispatch(types.HIDE_HANDLE_TIP)
}



