'use strict'

export default function (router) {
  router.map({
    '*': {
      component: function (resolve) {
        require(['./views/shop.vue'], resolve)
      }
    },
    '/': {
      name: 'shop',
      component: function (resolve) {
        require(['./views/shop.vue'], resolve)
      }
    },
    '/shop': {
      name: 'shop',
      component: function (resolve) {
        require(['./views/shop.vue'], resolve)
      }
    },
    '/signin': {
      name: 'signin',
      component: function (resolve) {
        require(['./views/sign-in.vue'], resolve)
      }
    },
    '/statistic': {
      name: 'statistic',
      component: function (resolve) {
        require(['./views/statistic.vue'], resolve)
      }
    },
    '/categories': {
      name: 'categories',
      component: function (resolve) {
        require(['./views/categories.vue'], resolve)
      }
    },
    '/category/:category_id': {
      name: 'category',
      component: function (resolve) {
        require(['./views/category.vue'], resolve)
      }
    },
    '/rooms': {
      name: 'rooms',
      component: function (resolve) {
        require(['./views/rooms.vue'], resolve)
      }
    },
    '/room/:room_id': {
      name: 'room',
      component: function (resolve) {
        require(['./views/room.vue'], resolve)
      }
    },
    '/workers': {
      name: 'workers',
      component: function (resolve) {
        require(['./views/workers.vue'], resolve)
      }
    },
    '/worker/:worker_id': {
      name: 'worker',
      component: function (resolve) {
        require(['./views/worker.vue'], resolve)
      }
    },
    '/products': {
      name: 'products',
      component: function (resolve) {
        require(['./views/products.vue'], resolve)
      }
    },
    '/product/:product_id': {
      name: 'product',
      component: function (resolve) {
        require(['./views/product.vue'], resolve)
      }
    },
    '/orders': {
      name: 'orders',
      component: function (resolve) {
        require(['./views/orders.vue'], resolve)
      }
    },
    '/order/:order_id': {
      name: 'order',
      component: function (resolve) {
        require(['./views/order.vue'], resolve)
      }
    },
    '/coupons': {
      name: 'coupons',
      component: function (resolve) {
        require(['./views/coupons.vue'], resolve)
      }
    },
    '/coupon/:coupon_id': {
      name: 'coupon',
      component: function (resolve) {
        require(['./views/coupon.vue'], resolve)
      }
    },
    '/profile': {
      name: 'profile',
      component: function (resolve) {
        require(['./views/profile.vue'], resolve)
      }
    }
  })
}



