import {
  SIGN_IN,
  SIGN_OUT
} from '../mutation-types'

const state = {
  isLogged: false,
  merchant: {}
}

const mutations = {
  [SIGN_IN] (state, merchant) {
    state.isLogged = true
    state.merchant = merchant
    localStorage.setItem('jc_merchant_access_token', merchant.access_token)
  },
  [SIGN_OUT] (state) {
    state.isLogged = false
    state.merchant = {}
    localStorage.removeItem('jc_merchant_access_token')
  }
}

export default {
  state,
  mutations
}



